* {
  font-family: Rockwell;
  font-weight: 400;
}
body {
  display: flex;
  align-items: center;
  justify-content: center;
}
.img {
  width: 220px;
  height: 330px;
}
.ul {
  padding: 0;
  list-style: none;
}
.list-item {
  display: inline-block;
  padding: 15px 28px;
  background-color: rgba(49, 206, 187, 0.24);
  border-radius: 20px;
  margin-bottom: 15px;
}

.list-item-2 {
  font-size: 24px;
  line-height: 28px;
  margin-bottom: 30px;

  color: #000000;
}

.list-item-3 {
  font-size: 18px;
  line-height: 21px;

  color: #31cebb;
  margin-bottom: 30px;
}
.btn {
  outline: 1px solid transparent;
  padding: 15px 122px 12px 122px;
  background: #31cebb;
  border-radius: 20px;
  border-color: transparent;
}
